.part5 {
  background: url(/static/media/bg.eabc8a1d.jpg);
  display: flex;
  flex-direction: column;
  padding-top: 22px;
  padding-bottom: 22px;
}
.part5 .content {
  position: relative;
  border: 7px solid #F4D990;
  border-radius: 7px;
  width: 88.5%;
  margin: auto;
  padding: 5px 20px;
  font-size: 18px;
  line-height: 29px;
  color: #F4D990;
}
.part5 .content span {
  font-family: aliHanYi;
  color: #fff;
}
.part5 .chartTitle {
  background: linear-gradient(134deg, #B620E0 0%, #3409CE 100%);
  border-radius: 35px;
  font-size: 18px;
  line-height: 37px;
  width: 62%;
  margin: auto;
  position: relative;
  z-index: 2;
  top: 18px;
  text-align: center;
  color: #fff;
}
.part5 .chart {
  flex: 1 1;
  position: relative;
  background: #fff;
  border-radius: 20px;
  overflow: hidden;
  width: 88.5%;
  margin: auto;
  padding-top: 30px;
}
.part5 .chart .chart-content {
  height: 100%;
}
