@font-face {
  font-family: 'ahui';
  font-display: swap;
  src: url('//at.alicdn.com/t/webfont_knuuio5cgb.eot');
  /* IE9*/
  src: url('//at.alicdn.com/t/webfont_knuuio5cgb.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('//at.alicdn.com/t/webfont_knuuio5cgb.woff2') format('woff2'), url('//at.alicdn.com/t/webfont_knuuio5cgb.woff') format('woff'), /* chrome、firefox */ url('//at.alicdn.com/t/webfont_knuuio5cgb.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url('//at.alicdn.com/t/webfont_knuuio5cgb.svg#阿里汉仪智能黑体') format('svg');
  /* iOS 4.1- */
}
.ahui-font {
  font-family: "ahui" !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.login {
  background: url(/static/media/bg.4f800ae0.png) no-repeat center center;
  background-size: 100% 100%;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.login-input {
  float: left;
  width: 70%;
  height: 7%;
  margin: 70% 0 0 10%;
  color: #fff;
  position: relative;
}
.login-input .input {
  float: left;
  width: 100%;
  height: 100%;
  background: transparent;
  border-radius: 22px;
  color: #fff;
}
.am-list-item .am-input-control input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.4) !important;
}
.am-list-item .am-input-control input {
  color: #fff !important;
}
.login-arr {
  top: 25%;
  right: 0;
  width: 10%;
  position: absolute;
  display: inline-block;
  cursor: pointer;
}
