.part10 {
  background: url(data:image/jpeg;base64,);
  font-family: aliHanYi;
}
.part10 .container {
  height: 100%;
  position: relative;
  top: -40px;
  display: flex;
  flex-direction: column;
}
.part10 .container .pic-group.group1 {
  display: flex;
  flex: 2 1;
  justify-content: flex-end;
}
.part10 .container .pic-group.group1 div {
  display: flex;
  flex: 1 1;
  text-align: center;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 20px;
  font-size: 24px;
  color: #fff;
}
.part10 .container .pic-group.group1 div p {
  margin-bottom: 10px;
}
.part10 .container .pic-group.group1 img {
  border-radius: 12px;
  background: #fff;
  padding: 5px;
  padding-right: 0;
}
.part10 .container .pic-group.group2 {
  margin: 30px 0;
  flex: 3 1;
  display: flex;
}
.part10 .container .pic-group.group2 div {
  flex: 1 1;
  display: flex;
  justify-content: center;
  font-size: 24px;
  color: #fff;
}
.part10 .container .pic-group.group2 div p {
  line-height: 32px;
  width: 24px;
}
.part10 .container .pic-group.group2 div p:last-child {
  margin-left: 10px;
  padding-top: 24px;
}
.part10 .container .pic-group.group2 img {
  border-radius: 12px;
  background: #fff;
  padding: 5px;
  padding-left: 0;
}
.part10 .container .pic-group.group3 {
  flex: 2 1;
  text-align: center;
}
.part10 .container .pic-group.group3 img {
  border-radius: 12px;
  background: #fff;
  padding: 5px;
}
