.part2 {
  background: url(data:image/jpeg;base64,);
  font-family: "aliHanYi";
  padding-top: 40px;
  position: relative;
}
.part2 p {
  padding-left: 10%;
  color: #F4D990;
  font-size: 18px;
  line-height: 32px;
}
.part2 p span {
  font-family: "PingFangSC-Semibold", "sans-serif", "Microsoft YaHei UI", "Microsoft YaHei";
