.part8 {
  background: url(data:image/jpeg;base64,);
  padding-top: 22px;
  display: flex;
  flex-direction: column;
}
.part8 .textContent {
  padding: 0 20px;
  font-family: aliHanYi;
  color: #fff;
  font-size: 22px;
  line-height: 32px;
}
.part8 .textContent span {
  color: #FFE700;
}
.part8 .chartTitle {
  color: #fff;
  font-size: 18px;
  text-align: center;
  width: 40%;
  height: 42px;
  line-height: 42px;
  background: url(data:image/png;base64,);
  background-size: cover;
  margin: 20px 0;
}
.part8 .all {
  margin: 10px 20px;
  display: flex;
}
.part8 .all .pic {
  flex: 1 1;
}
.part8 .all .allCenter {
  flex: 2 1;
  text-align: center;
  font-size: 12px;
  margin: 0 5px;
}
.part8 .all .allCenter .circleContainer {
  margin: 5px 0;
}
.part8 .all .allCenter .circleContainer .boyCircle {
  background: #00EBFF;
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 8px;
  color: #fff;
}
.part8 .all .allCenter .circleContainer .girlCircle {
  background: #F7B500;
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 8px;
  color: #fff;
}
.part8 .all .allCenter .numContainer {
  display: flex;
}
.part8 .all .allCenter .numContainer span {
  flex: 1 1;
  font-size: 18px;
  padding: 5px 0;
}
.part8 .all .allCenter .numContainer span.boyNum {
  background: rgba(29, 188, 251, 0.42);
  color: #00EBFF;
}
.part8 .all .allCenter .numContainer span.girlNum {
  background: rgba(247, 181, 0, 0.4);
  color: #F7B500;
}
.part8 .chartContainer {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  position: relative;
  padding-top: 40px;
}
.part8 .chartContainer .containerTitle {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #5028E1;
  width: 60%;
  height: 32px;
  line-height: 32px;
  text-align: center;
  border-radius: 10px;
  color: #fff;
}
.part8 .chartContainer .chart {
  flex: 1 1;
}
.part8 .chartContainer .chart .chart-content {
  height: 100%;
}
