
/* 重置样式 */
html{font-family:"Microsoft YaHei UI","Microsoft YaHei",sans-serif;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%}
html,body,div,object,iframe,applet,object,h1,h2,h3,h4,h5,h6,p,blockquote,pre,address,dl,dt,dd,ol,ul,li,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,menu,nav,output,ruby,section,summary,time,mark,audio,video,progress{margin:0;padding:0;border:0;vertical-align:baseline;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;}
li{list-style:none}
a{background-color:transparent;text-decoration:none}
a:active,a:hover{outline:0}
b,strong{font-weight:bold}
canvas{display:block}

/* 媒介查询兼容大分辨率*/
@media (min-width:992px) {
    body,html{
        overflow: hidden;
    }
    #root{
        float: left;
        width: 375px;
        height: 667px;
        margin: 20px 0 0 100px;
    }       
    .my-carousel .item{
        height: 667px !important;
    }
}

/* 特殊字体 */
/* 阿里汉仪智能黑体  aliHanYi*/
@font-face {
    font-family: 'aliHanYi';
    font-display: swap;
    src: url('//at.alicdn.com/t/webfont_o5aodb2q2q.eot'); /* IE9*/
    src: url('//at.alicdn.com/t/webfont_o5aodb2q2q.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('//at.alicdn.com/t/webfont_o5aodb2q2q.woff2') format('woff2'),
    url('//at.alicdn.com/t/webfont_o5aodb2q2q.woff') format('woff'), /* chrome、firefox */
    url('//at.alicdn.com/t/webfont_o5aodb2q2q.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url('//at.alicdn.com/t/webfont_o5aodb2q2q.svg#阿里汉仪智能黑体') format('svg'); /* iOS 4.1- */
  }
