.part3 {
  background: url(/static/media/bg.eabc8a1d.jpg);
  padding: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.part3 .img {
  width: 100%;
  overflow: hidden;
  border: 7px solid #fff;
  border-radius: 10px;
  font-size: 0;
}
.part3 .img img {
  width: 100%;
}
.part3 .list {
  flex: 1 1;
  margin-top: 20px;
  border: 6px solid #F4D990;
  padding: 10px;
  border-radius: 10px;
  overflow-y: auto;
}
.part3 .list .student-item {
  display: flex;
  text-align: center;
  color: #F4D990;
  padding: 5px;
}
.part3 .list .student-item .name {
  color: #FFEFC5;
  flex: 1 1;
}
.part3 .list .student-item .place {
  flex: 1 1;
}
.part3 .list .student-item .date {
  flex: 2 1;
}
.part3 .list .student-item .major {
  flex: 3 1;
}
